<template>
 <div>
    <page-layout>
      <div class="mdl-tabs mdl-js-tabs mdl-js-ripple-effect">
        <div class="mdl-tabs__tab-bar mdl-cell--bottom">
          <a href="#list" class="mdl-tabs__tab is-active">Products</a>
          <a href="#product" class="mdl-tabs__tab">Add</a>
          <a href="#production" class="mdl-tabs__tab">Production</a>
        </div>
        <div class="mdl-tabs__panel is-active" id="list">
         <product-list-view></product-list-view>
        </div>
        <div class="mdl-tabs__panel" id="product">
          <product-create-view></product-create-view>
        </div>
        <div class="mdl-tabs__panel" id="production">
         <production-list-view></production-list-view>
        </div>
      </div>
    </page-layout>
 </div>
</template>
<script>
  import { PageLayout } from 'LAYOUTS_COMPONENTS'
  import { ProductListView, ProductCreateView } from '.'
  import { ProductionListView } from 'PRODUCTION_APP/components'
  export default {
    name: 'vendors-view',
    components: {
      PageLayout,
      ProductListView,
      ProductionListView,
      ProductCreateView
    }
  }
</script>



